import { Component } from '@angular/core';
import { NavParams } from 'ionic-angular';

import * as THREE from 'three';
import { TYPES }  from '../../models/interfaces/duct.properties';
import { Duct }  from '../../models/interfaces/duct.interface';
import { DuctFactory } from '../../models/objects/factory';

/**
 * Generated class for the PopoverComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'popover',
  templateUrl: 'popover.html'
})

export class PopoverComponent {

  type_list = TYPES;
  protected duct: Duct;
  protected _scene: any;

  constructor( private navParams: NavParams ) {

  }

  ngOnInit() {
    if (this.navParams.data) {
      this._scene = this.navParams.data.scene;

      // this.background = this.getColorName(this.contentEle.style.backgroundColor);
      //this.duct.color = this.getColorName(this.duct.color);
      //console.log(this._duct.geometry.name);
      // this.setFontFamily();
      //this.setDuctType();
    }
  }

  //This method is going to get the duct's color
  // getColorName(background: any) {
  //   let colorName = 'white';
  //
  //   if (!background) return 'white';
  //
  //   for (var key in this.colors) {
  //     if (this.colors[key].bg === background) {
  //       colorName = key;
  //     }
  //   }
  //
  //   return colorName;
  // }

  //TODO implement the method to change the type of the duct
  // setDuctType(){
  //   if (this.duct.type) {
  //     //TODO
  //     this.type_list = this.duct.type;
  //   }
  // }

  changeBackground(color: any) {
    this.duct.material.color = new THREE.Color(color);
  }


  changeDuctType($event) {

    if ($event) {
      console.log(this._scene.children);
      if(this._scene.children > 5){
          this._scene.remove(this._scene.getObjectById(this._scene.children[5].id));
          this._scene.update();
      }
      this.duct = DuctFactory.createDuct($event);
      this.duct.draw();
      this._scene.add( this.duct.mesh );
      //this.duct.type = this.type_list;
    }
  }
  // changeFontFamily() {
  //   if (this.fontFamily) this.textEle.style.fontFamily = this.fontFamily;
  // }


}



